/*
 * @Author: Logic
 * @Date: 2019-06-19 16:56:29 
 */
import axios from '@/axios.js'

var api = {

/*************************************************************************
 *          道岔类型管理 API
 *************************************************************************
 */
    /**
     * 获取道岔列表
     */
    getRoadClassify() {
        return axios.get('/turnouttype/')
    },
    /**
     * 删除道岔分类
     * @param {*id} 
     */
    delRoadClassify(params) {
        return axios.get('/turnouttype/delete/' + params);
    },
    /**
     * 创建道岔分类
     * @turnouttypename {*道岔分类名称 编号}  
     * @reamke {*备注}
     */
    addRoadClassify(params) {
        return axios.post('/turnouttype/save', params);
    },

    /**
     * 修改道岔分类
     * @id {*要修改的id}
     * @turnouttypename {*道岔分类名称 编号}  
     * @reamke {*备注}  
     */
    editRoadClassify(id, params) {
        return axios.post('/turnouttype/save/' + id, params);
    },

/*************************************************************************
 *          测量标准 API
 *************************************************************************
 */
    /**
     * 获取道岔标准列表
     * XXXXXX
     */
    getTesStandard(params) {
        return axios.get('/turnoutstandard/', params);
    },
    /**
     * 获取符号
     */
    getCalculationstandard(params) {
        return axios.get('/calculationstandard/', params);
    },

    /**
     * 新增测量标准模板
     */
    addMeasurementstandard(params) {
        return axios.post('/measurementstandard/insert', params);
    },
    /**
     * 获取测量标准模板列表
     * @param {*} id 
     * @param {*第几页} pageNum 
     */
    getMeasurementstandard(id, pageNum) {
        return axios.get('/measurementstandard/turnoutstandard/' + id + '?pageNum=' + pageNum + '&pageSize=10');
    },


    /**
     * 获取测量标准模板列表
     * @param {*} id
     * @param {*} pageNum 
     */
    deleteMeasurementstandard(params) {
        return axios.get('/measurementstandard/delete' + params);
    },




/*************************************************************************
 *          质量分析标准 API
 *************************************************************************
 */
    /**
     * 获取质量分析标准分类
     */
    getQualitystandard(params) {
        return axios.get('/qualitystandard/findidentifiernum', params)
    },

    /**
     * 质量分析标准分类 新增
     * identifiernum 分类
     * msg 说明
     * msg1  标准
     * num 编号
     */
    newQualitystandard(params) {
        return axios.post('/qualitystandard/save', params)
    },


    /**
     * 质量分析标准分类 修改
     * identifiernum 分类
     * msg 说明
     * msg1  标准
     * num 编号
     */
    changeQualitystandard(id, params) {
        return axios.post('/qualitystandard/save/' + id, params)
    },
    

    /**
     * 质量分析标准分类 删除
     */
    delQualitystandard(id) {
        return axios.get('/qualitystandard/delete/' + id)
    },
    
    /**
     * 获取道岔列表
     */
    getTurnouttype() {
        return axios.get('/turnouttype/')
    },
    

    /**
     * 道岔标准 新增
     * @param {*关联质量分析标准} qualityid 
     * @param {*道岔分类} turnoutid 
     * @param {*道岔标准名称} turnoutstandard 
     */
    newTurnoutstandard(params) {
        return axios.post('/turnoutstandard/save', params)
    },
    
    /**
     * 道岔标准 删除
     * @param {*} id 
     */
    delTurnoutstandard(id) {
        return axios.get('/turnoutstandard/delete/' + id)
    },
    

    /**
     * 道岔标准 修改
     * @param {*关联质量分析标准} qualityid 
     * @param {*道岔分类} turnoutid 
     * @param {*道岔标准名称} turnoutstandard 
     */
    editTurnoutstandard(id, params) {
        return axios.post('/turnoutstandard/save/' + id, params)
    },


    /**
     * 轨距测量标准  列表
     */
    getGaugestandard() {
        return axios.get('/gaugestandard/')
    },
    
    /**
     * 轨距测量标准 新增
     * @param {*关联项点} turnoutid
     * @param {*里程} mileage 
     * @param {*轨距} gauge 
     */
    newGaugestandard(params) {
        return axios.post('/gaugestandard/save', params)
    },
    
    /**
     * 系统日志 列表
     * @param {*关联项点} pageSize
     * @param {*里程} pageNum 
     */
    getLog(pageSize, pageNum) {
        return axios.get('/Systemlog/selectAlllog?pageSize=' + pageSize + '&pageNum=' + pageNum)
    },



    /**
     * 质量分析标准 列表
     * @param {*关联项点} pageSize
     * @param {*里程} pageNum 
     */
    geTqualitystandard() {
        return axios.get('/qualitystandard/')
    },

    // 获取小车数据 列表
    getSmartCarData() {
        return axios.get('/detectionresult/getSmartCarData')
    },
    

/*************************************************************************
 *          线路 API
 *************************************************************************
 */

    // 线路 列表
    getSelectAll() {
        return axios.get('/trainroute/selectAll?pageNum=0&pageSize=10000')
    },
    
    // 站点 列表
    getLinesite(routeid) {
        return axios.get('/linesite/selectbyrouteid?pageNum=0&pageSize=10000&routeid=' + routeid)
    },
    
    // 站点 删除
    deleteLinesite(id) {
        return axios.get('linesite/delete?id=' + id)
    },
    
    
    
    
    /**
     * 站点 新增
     * @param {*线路id} routeid 
     * @param {*站点名称} sitename 
     */
    newLinesite(param) {
        return axios.post('/linesite/insert', param)
    },

    
    /**
     * 新增线路
     * @param {*线路名称} routename 
     */
    newTrainrouteinsert(params) {
        return axios.post('trainroute/insert', params)
    },
    
    /**
     * 删除线路
     * @param {*线路id} id
     * @param {*线路名} routename
     */
    delTrainrouteinsert(id) {
        return axios.get('/trainroute/delete?id=' + id)
    },
    
    /**
     * 修改线路
     * @param {*线路id} id
     */
    changeLine(params) {
        return axios.post('/trainroute/updatebyid', params)
    },
    
    /**
     * 首页检测项目 列表
     * @param {*站点id} id
     * @param {*页码} pageNum 
     * @param {*每页数量} pageSize 
     */
    getMeasurementproject(id, pageNum, pageSize) {
        return axios.get('/measurementproject/selectBy?id=' + id + '&pageNum=' + pageNum + '&pageSize=' + pageSize)
    },



    /**
     * 站点设置 获取
     * @param {*业主通知} noticeowner 
     * @param {*施工进展} progressconstruction 
     * @param {*项目概况} projectoverview 
     * @param {*站点id} rid 
     * @param {*天气id} weatherid 
     * @param {*地理位置 -- 废弃} geographical
     * @param {*天气 -- 废弃} weather 
     */
    newMainpageinformaction(params) {
        return axios.post('/mainpageinformaction/updatebyid', params)
    },


    /**
     * 站点设置 修改
     * @param {*修改} id
     * @param {*业主通知} noticeowner
     * @param {*施工进展} progressconstruction 
     * @param {*项目概况} projectoverview 
     * @param {*站点id} rid
     * @param {*天气id} weatherid
     * @param {*地理位置 -- 废弃} geographical
     * @param {*天气 -- 废弃} weather
     */
    changeMainpageinformaction(params) {
        return axios.post('/mainpageinformaction/updatebyid', params)
        // return axios.post('/mainpageinformaction/insert', params)
    },




    /**
     * 获取首页项目信息
     * rid 站点id
     */
    getMainpageinformactionSelectbyrid(rid) {
        return axios.get('/mainpageinformaction/selectbyrid?rid=' + rid)
    },
    

    // 获取站点信息
    getMainpageinformaction(rid) {
        return axios.get('/mainpageinformaction/selectbyrid?rid=' + rid)
    },

    // 获取城市信息
    getCity() {
        return axios.get('/city/')
    },
    

    

    /**
     * 登录
     * @param {*用户名} ausername
     * @param {*密码} apwd 
     */
    userLogin(ausername, apwd) {
        return axios.get('administrator/adminlogin?ausername=' + ausername + '&apwd=' + apwd)
    },

    /**
     * 获取天气
     */
    getWeather(id) {
        return axios.get('/city/' + id)
    }
    
}
export default api;


