<template>
  <div id="app">
    <el-container>
        <el-header class="Header">
            <div class="left">
                <img class="logo" src="@/assets/img/logo.png" alt="">
                <p>高速铁路道岔参数管理系统</p>
            </div>
            <div class="right" @click="myOut">
                <img src="@/assets/img/tuichudenglu.png" alt="">
                <p>退出登陆</p>
            </div>
        </el-header>
        <el-container>
            <el-aside width="200px" class="Aside">
              <my-nav />
                <!-- <router-link to="/home" class="nav-item">
                    <img src="@/assets/img/shouye.png" alt="">
                    <p>首页</p>
                </router-link> -->
            </el-aside>
            <el-main class="Main">
              <router-view/>
            </el-main>
        </el-container>
    </el-container>
    <!-- <router-view/> -->
    <my-login class="myLogin" v-show="loginShow"></my-login>
    <!-- 登录 -->
  </div>
</template>
<script>
import myNav from '@/components/Nav.vue';
import myLogin from '@/components/myLogin.vue';
export default {
  components: {
    myNav,
    myLogin
  },
  data() {
    return {
      loginShow: true
    }
  },
  methods: {
    myOut() {
      this.$confirm('是否退出登录?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.loginShow = true;
          localStorage.removeItem('USERINFO');
          this.$message({
            type: 'success',
            message: '退出登录!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          });          
        });
    }
  },
  created() {
    if(localStorage.getItem("USERINFO")) {
      this.loginShow = false;
    }
  },
}
</script>
<style lang="stylus">
*
  margin 0
  padding 0
body
  background-image url('./assets/img/bg.jpg')
  background-size content
  background-repeat no-repeat


#app
  .Header
    color #fff
    display flex
    justify-content space-between
    align-items center
    .left 
        display flex
        justify-content flex-start
        align-items center
        .logo
            width 35px
        p 
            margin-left 10px
    .right
        display flex
        align-items center
        cursor pointer
        p
            margin-left 10px
.Aside
    padding 20px 0 0 0px
    margin-left 20px
    .nav-item
        display flex
        align-items center
        color #fff
        text-decoration none
        & > p 
            margin-left 5px
.myLogin
  position fixed
  left 0
  top 0
  z-index 1

</style>



