<template>
  <div class="home">
    <!-- Header -->
    <el-row class="Header">
      <el-col :span="24">
        <div class="grid-content bg-purple-dark">
          <div class="select-item">
            <!-- 线路 -->
            <p>路线：</p>
            <el-dropdown class="select_" @command="changeLine">
              <span class="el-dropdown-link">
                {{save.line.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-for="item in line" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <!-- 站点 -->
            <p style="margin-left: 20px;">站点：</p>
            <el-dropdown class="select_"  @command="changeSite">
              <span class="el-dropdown-link">
                {{save.site.sitename}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-for="item in site" :key="item.id" :command="item">{{item.sitename}}</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
      </el-col>
    </el-row>
    <!-- Header END -->
    
    <el-row>
      <!-- 表格 -->
      <el-col :span="14">
        <div class="grid-content bg-purple">
          <div class="tableCon">
                <p class="title">检测结果</p>
                            <el-table
                            :data="tableData"
                            style="width: 100%;border: 1px solid #eee;border-bottom: none;">
                            <el-table-column
                                prop="ausername"
                                label="项目名称"
                                width="280">
                            </el-table-column>
                            <el-table-column
                                prop="ptime"
                                label="检测时间"
                                width="280">
                            </el-table-column>
                            <el-table-column
                                prop="warningstatistics"
                                label="预警统计">
                            </el-table-column>
                        </el-table>

                      <div class="fy">
                        <el-pagination
                            background
                            layout="prev, pager, next"
                            :total="tableData.length">
                        </el-pagination>
                      </div>
          </div>
        </div>
      </el-col>
      <!-- 天气 -->
      <el-col :span="10">
        <div class="grid-content bg-purple-light">
          <div class="wratter">
            <div class="con">
                <el-row>
                  <el-col :span="10">
                    <div class="grid-content bg-purple">
                        <div class="weather">
                          <img style="width: 60px; margin-left: 20px;" src="@/assets/img/weather/暴雨.png" alt="">
                          <p style="margin-top: 10px;">温度：{{save.weather.temp}}</p>
                          <p>湿度：{{save.weather.SD}}</p>
                          <p>气压：{{save.weather.AP}}</p>
                          <p>{{save.weather.WD}}：{{save.weather.WS}}</p>
                          <p>城市：{{save.weather.city}}</p>
                        </div>
                    </div>
                  </el-col>
                  <el-col :span="14">
                    <div class="grid-content bg-purple-light">
                      <div class="text-con">
                        <p> 项目概况： {{save.projectMsg.projectoverview}}</p>
                        <p> 业主通知： {{save.projectMsg.noticeowner}}</p>
                        <p> 地理位置： {{save.projectMsg.geographical}}</p>
                        <p> 施工进度： {{save.projectMsg.progressconstruction}}</p>
                      </div>
                    </div>
                  </el-col>
                </el-row>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import api from "@/api/index.js";
  export default {
    name: 'home',
    components: {

    },
    data () {
      return {
        page: '0',
        line: [],
        site: [],
        save: {
          line: {},
          site: {},
          projectMsg: {},
          weather: {}
        },
        tableData: [],
        siteID: ''
      }
    },
    methods: {

      // 改变线路
      changeLine (choose) {
        console.log(choose)
        var _this = this;
        this.save.line = choose;
        api.getLinesite(choose.id).then(res => {
          console.log(res.data.list)
          this.site = res.data.list;
          this.save.site = res.data.list[0];
          this.siteID = res.data.list[0].id;
        })
        this.getMainpageinformactionSelectbyrid(this.save.site.id);
      },
      // 获取线路
      getSelectAll() {
        api.getSelectAll().then(res => {
          console.log(res.data.list)
          this.line = res.data.list;
          this.save.line = res.data.list[0]
        })
      },
      // 改变站点
      changeSite(choose) {
        console.log(choose);
        this.save.site = choose;
        this.siteID = choose.id;
        this.getMainpageinformactionSelectbyrid(this.save.site.id);
      },
      // 获取站点
      getLinesite(routeid) {
        api.getLinesite(routeid).then(res => {
          console.log('****')
          console.log(res)
          console.log(res.data.list)
          this.site = res.data.list;
          this.save.site = res.data.list[0];
          this.siteID = res.data.list[0].id;
          this.getMainpageinformactionSelectbyrid(this.save.site.id)
        })
      },
      // 检测项目 列表
      getMeasurementproject(id, pageNum) {
        api.getMeasurementproject(id, pageNum, 10).then(res => {
          console.log(res.data.list)
          this.tableData = res.data.list;
        })
      },
      // 获取首页项目信息
      getMainpageinformactionSelectbyrid(rid) {
        api.getMainpageinformactionSelectbyrid(rid).then(res => {
          console.log('*********************')
          console.log(res.data.msg)
          this.save.projectMsg = res.data.msg;
          this.getWeather(this.save.projectMsg.weatherid);
        })
      },
      // 获取天气
      getWeather(id) {
        api.getWeather(id).then(res => {
          console.log('**********************')
          console.log(JSON.parse(res.data.result))
          this.save.weather = JSON.parse(res.data.result).weatherinfo;
        })
      }
    },
    watch: {
      siteID(val, newval) {
          let _this = this;
          console.log(val)
          console.log(newval)
          this.getMeasurementproject(val, this.page);
      }
    },
    created() {
      let _this = this;
      api.getSmartCarData().then(res => {
        console.log(res)
      })
      this.getSelectAll();
      setTimeout(() => {
        console.log(this.save)
        _this.getLinesite(_this.save.line.id);
      }, 500)
    },
  }
</script>
<style lang="stylus" scoped>
.Header
  color #fff
  .select-item
    display flex
    align-items center
    color #fff
    .select_
      color #fff
      border 1px solid #fff
      padding 4px 10px
      border-radius 4px
      cursor pointer
.tableCon
  background #ffffff
  padding 20px
  border-radius 12px
  margin-top 20px
  .title
    margin-bottom 12px
  .fy 
    display flex
    margin-top 20px
    justify-content center
.wratter
  padding 20px
  .con
    background #fff
    padding 20px
    border-radius 12px




</style>



