<template>
    <div class="login">
      <el-row>
        <el-col :span="24">
          <div class="grid-content bg-purple-dark">
            <div class="top">
              <img src="@/assets/img/logo.png" alt="">
              <p>高速铁路道岔参数管理系统</p>
            </div>
          </div>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="24">
          <div class="grid-content bg-purple-dark">
            <div class="content">
              <div class="left">
                <p>用户登陆/USER LOGIN</p>
                <div class="input-item">
                  <i class="user"></i>
                  <input type="text" v-model="ausername" placeholder="请输入用户名">
                </div>
                <div class="input-item">
                  <i class="pwd"></i>
                  <input type="password" v-model="apwd" placeholder="请输入密码">
                </div>
                <div class="submit" @click="userLogin(ausername, apwd)">登录</div>
              </div>
              <div class="right">
                <p>Android 版下载</p>
                <img src="@/assets/img/logo.png" alt="">
                <p>使用手机扫描二维码</p>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="24">
          <div class="grid-content bg-purple-dark">
            <div class="bottom">
                <p class="t">Copyright @ 2018 SINOMIS. ALL Rights Reserved</p>
                <div class="c">
                  <p class="left">
                    开发单位：
                  </p>
                  <div class="right">
                    <p>中铁第五勘察设计院集团有限公司</p>
                    <p>中铁三局集团有限公司</p>
                  </div>
                </div>
            </div>
          </div>
        </el-col>
      </el-row>




    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
  data () {
    return {
      ausername: '',
      apwd: ''
    }
  },
  methods: {
    userLogin(ausername, apwd) {
      if(!ausername) {
        this.$message({
          type: 'error',
          message: '用户名不正确!'
        });
        return;
      }
      if(!apwd) {
        this.$message({
          type: 'error',
          message: '请输入密码!'
        });
        return;
      }
      api.userLogin(ausername, apwd).then(res => {
        console.log(res.data)
        if(res.data.stats == "fail") {
          this.$message({
            type: 'error',
            message: res.data.message
          });
        }
        if(res.data.stats == "success") {
            this.$message({
              type: 'success',
              message: '登录成功!'
            });
            // this.showState()
            var str = JSON.stringify(res.data);
            console.log(str)
            localStorage.setItem("USERINFO", str)
        }
      })
    }
  },
}
</script>

<style lang="stylus" scoped>
.login
  width 100%
  height 100vh
  background-image url('../assets/img/login-bg.jpg')
  display flex
  flex-direction column
  align-items center
  color #fff
  .top
    display flex
    align-items center
    font-size 22px
    margin-top 80px
    p 
      margin-left 12px
  .content
    display flex
    align-items center
    position fixed
    left 50%
    top 50%
    transform translate(-50%, -50%)
    .left 
      background #fff
      color #222
      width 350px
      height 300px
      display flex
      flex-direction column
      justify-content center
      align-items center 
      p 
        font-weight bold
        padding-bottom 12px
      .input-item 
        position relative
        margin-bottom 10px
        input 
          padding 10px 10px 10px 35px
          box-sizing border-box
          width 240px
          border 3px solid rgb(229, 229, 229)
          background rgba(232,240,254, 1)
          outline none
        i
          display block
          width 26px
          height 26px
          position absolute
          left 5px
          top 50%
          transform translateY(-50%)
          background-size 100% 100%
          background-repeat norepeat
        .user
          background-image url('../assets/img/user.png')
        .pwd
          background-image url('../assets/img/password.png')
      .submit
        background rgb(26, 76, 147)
        width 240px
        display flex
        align-items center
        justify-content center
        color #fff
        padding 8px 0
        margin-top 10px
        cursor pointer
    .right 
      display flex
      flex-direction column
      align-items center
      justify-content center
      background #003883
      width 280px
      height 300px
      img 
        margin 10px 0
        border 1px solid #fff
        width 100px
        height 100px
  .bottom 
    position fixed
    left 50%
    transform translateX(-50%)
    bottom 40px
    p 
      line-height 2em
    .c 
      display flex
      justify-content center

</style>



