import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {// 首页
      path: '/home',
      name: 'home',
      component: Home
    },
    {// 道岔类型管理
      path: '/typeMange',
      name: 'typeMange',
      component: () => import('./views/systemManage/typeMange.vue')
    },
    {// 测量标准
      path: '/measureStandard',
      name: 'measureStandard',
      component: () => import('./views/systemManage/measureStandard.vue')
    },
    {// 测量标准模板
      path: '/measureStandardTemplate',
      name: 'measureStandardTemplate',
      component: () => import('./views/systemManage/measureStandardTemplate.vue')
    },
    {// 轨距测量标准
      path: '/gaugeMeasureStandard',
      name: 'gaugeMeasureStandard',
      component: () => import('./views/systemManage/gaugeMeasureStandard.vue')
    },
    {// 系统日志
      path: '/systemLog',
      name: 'systemLog',
      component: () => import('./views/systemManage/systemLog.vue')
    },
    {// 站点设置
      path: '/siteSetting',
      name: 'siteSetting',
      component: () => import('./views/systemManage/siteSetting.vue')
    },
    {// 线路
      path: '/line',
      name: 'line',
      component: () => import('./views/systemManage/line.vue')
    },
    {// 站点
      path: '/site',
      name: 'site',
      component: () => import('./views/systemManage/site.vue')
    },
    {// 人员管理
      path: '/peopleManage',
      name: 'peopleManage',
      component: () => import('./views/systemManage/peopleManage.vue')
    },
    {// 质量分析标准
      path: '/tqualitystandard',
      name: 'tqualitystandard',
      component: () => import('./views/systemManage/tqualitystandard.vue')
    },




    {// 测量数据
      path: '/getSmartCarData',
      name: 'getSmartCarData',
      component: () => import('./views/index/getSmartCarData.vue')
    },

    {// 测量档案
      path: '/surveyArchives',
      name: 'surveyArchives',
      component: () => import('./views/index/surveyArchives.vue')
    },

    {// 质量分析
      path: '/geTqualitystandard',
      name: 'geTqualitystandard',
      component: () => import('./views/index/geTqualitystandard.vue')
    },



    
    
  ]
})



